import React, { useState } from 'react';
import {NavLink} from 'react-router-dom';

import { Container, Row, Col } from 'react-bootstrap';

// icons
import Logo from "../../Assets/DCISM_LOGO.png";




// css
import './Navbar.css'


function UserNavbar() {
    return (
        
        <div className = "sidenav d-print-none">
            {/* logo */}
            <div className = "logo comm-btn pt-5">
                <img src = {Logo} alt = 'Logo' />
            </div>

            {/* tabs */}
            <NavLink style={{transition: "all .3s"}} to = "/RoomIssueAdmin" className={({isActive}) => (isActive ? "content content-selected": "content")}>
                <div>
         
                    <span className='mx-3'>Room Issue </span>
                </div>
            </NavLink>

            {/* logout */}
            <div className='logout-container'>
            
                <span className="mx-2">LOGOUT</span>
            </div>
        </div>
    );
};

export default function AdminNavbar() {
    const [showManage, setShowManage] = useState(false);
    const [showContribution, setShowContribution] = useState(false);
    const [inactive, setInactive] = useState(true);

    return (  
        <div onClick={() => setInactive(!inactive)} class="hamburger-menu">
        <input id="menu__toggle" type="checkbox" />
        <label class="menu__btn" for="menu__toggle">
        </label>
        <div className = "sidenav d-print-none">
            <div className = "margin">
            {/* logo */}
            <Row className="logo pt-3 pb-3 p-0 m-0 " style={{width:"100%"}}>
                <img src={Logo} className ='logoSize' alt='Logo' />
            </Row>
                <div  className = "marginTab">
            {/* tabs */}
            <NavLink style={{transition: "all .3s"}} to = "/RoomIssueAdmin" className={({isActive}) => (isActive ? "content content-selected": "content")}>
                <div>
                
                    <span style={{fontSize:"16px"}}>Room Issue</span>
                </div>
            </NavLink>
            <NavLink style={{transition: "all .3s"}} to = "/ToolsRequest" className={({isActive}) => (isActive ? "content content-selected": "content")}>
                <div>
            
                    <span style={{fontSize:"16px", }}>ToolsRequest</span>
                </div>
            </NavLink>
            
            <NavLink style={{transition: "all .3s"}} to = "/FinanceRequest" className={({isActive}) => (isActive ? "content content-selected": "content")}>
                <div>
                
                    <span style={{fontSize:"16px"}}>Finance Request</span>
                </div>
            </NavLink>


            
            <NavLink style={{transition: "all .3s"}} to = "/" className={({isActive}) => (isActive ? "content content-selected": "content")}>
                
                <div >
             
                    <span style={{fontSize:"16px"}} className="">logout</span>
                </div>
            </NavLink>
            </div>
            </div>
        </div>

    <ul class={`menu__box ${inactive ? "inactive" : ""}`}>
    <div className = "logo pt-0 pb-3 p-0 m-0" style={{width:"100%"}} >
                <img src = {Logo} className = 'logoSize' alt = 'Logo' />
            </div>
            <NavLink style={{transition: "all .3s"}} to = "/RoomIssueAdmin" className={({isActive}) => (isActive ? "content content-selected": "content")}>
                <div>
             
                    <span style={{fontSize:"16px"}}>Room Issue </span>
                </div>
            </NavLink>
            <NavLink style={{transition: "all .3s"}} to = "/ToolsRequest" className={({isActive}) => (isActive ? "content content-selected": "content")}>
                <div>
                
                    <span style={{fontSize:"16px"}}>ToolsRequest</span>
                </div>
            </NavLink>
            <NavLink style={{transition: "all .3s"}} to = "/FinanceRequest" className={({isActive}) => (isActive ? "content content-selected": "content")}>
                <div>
                
                    <span style={{fontSize:"16px"}}>Finance Request</span>
                </div>
            </NavLink>

           
            
            <NavLink style={{transition: "all .3s"}} to = "/">
                <div className='content'>
                
                    <span style={{fontSize:"16px"}} className="">logout</span>
                </div>
            </NavLink>
            </ul>
        </div>
    );
};

