import React from "react"
import { NavLink, useHref } from "react-router-dom"
import "./Modal.css";
import "../common.css"
import {Row, Col, Container, Form, InputGroup} from 'react-bootstrap'

export default function StatusModal({ setOpenModal1 }){
    return(
       
      <div className="modalContainer">
        <div className="title">Finance Request Status</div>
                   
        <Form className="formtext" style={{marginTop:"20px"}}>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                
             <Form.Label>Finance ID</Form.Label>
              <Form.Control
                type="text"
                placeholder="Finance ID"
                autoFocus
              />
             
            </Form.Group>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Label>Description</Form.Label>
              <Form.Control as="textarea"   placeholder="Description Update" rows={3} />
            </Form.Group>
          </Form>
        {/* <div className="title">
          <h1>Are You Sure You Want to Continue?</h1>
        </div>
        <div className="body">
          <p>The next page looks amazing. Hope you want to go there!</p>
        </div> */}
        <div className="footer" >
          <button
            onClick={() => {
              setOpenModal1(false);
            }}
            id="cancelBtn"
          >
            Cancel
          </button>
          <button>Denied</button>
          <button>Approved</button>
        </div>
      </div>
   
    )
}
