import React from "react"
import { NavLink, useHref } from "react-router-dom"
import "./Modal.css";
import "../common.css"
import {Row, Col, Container, Form, InputGroup} from 'react-bootstrap'

export default function Modal({ setOpenModal }){
    return(
       
      <div className="modalContainer">
        <div className="title">Report Issue</div>
                   
        <Form className="formtext" style={{marginTop:"20px"}}>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                <Form.Label>Complainant Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Complainant Name"
                autoFocus
              />
                <Form.Label>Email address</Form.Label>
              <Form.Control
                type="email"
                placeholder="name@example.com"
                autoFocus
              />
             <Form.Label>Issue</Form.Label>
              <Form.Control
                type="text"
                placeholder="Issue"
                autoFocus
              />
                   <Form.Label>Room</Form.Label>
              <Form.Control
                type="text"
                placeholder="Room"
                autoFocus
              />
            </Form.Group>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Label>Description</Form.Label>
              <Form.Control as="textarea"   placeholder="Description" rows={2} />
            </Form.Group>
          </Form>
        {/* <div className="title">
          <h1>Are You Sure You Want to Continue?</h1>
        </div>
        <div className="body">
          <p>The next page looks amazing. Hope you want to go there!</p>
        </div> */}
        <div className="footer" >
          <button
            onClick={() => {
              setOpenModal(false);
            }}
            id="cancelBtn"
          >
            Cancel
          </button>
          <button>Save</button>
        </div>
      </div>
   
    )
}
